var reactMixin = require('react-mixin');
var {Button} = Tingle;

class Page extends React.Component {

  constructor(props) {
    super(props);
  }

  handleClick() {
    console.log('onClick')
  }

  render() {
    return (
      <div className="page-button">
        <Button type="primary" onClick={this.handleClick}>  </Button>
        <Button type="secondary" onClick={this.handleClick}>  </Button>
        <Button type="danger" onClick={this.handleClick}></Button>
        <Button type="primary" size="m" onClick={this.handleClick}></Button>
        <Button type="primary" size="s" onClick={this.handleClick}></Button>
        <Button disabled={true}></Button>
        <Button disabled={true} size="m"></Button>
        <Button disabled={true} size="s"></Button>
      </div>
    );
  }
}

reactMixin.onClass(Page, ReactRouter.Navigation);

module.exports = Page;



