let {Group,Button,Tip,SelectField,NumberField} = Tingle;

class Module extends React.Component {
  constructor(props) {
    super(props);
    var formatDataValue = SelectField.formatDataValue([
      'HH:mm',
      'yyyy-MM-dd',
      'yyyy-MM-dd HH:mm:ss'
    ]);
    var pickTypeDataValue = SelectField.formatDataValue([
      {
        text: '',
        value: 'time'
      },
      {
        text: '',
        value: 'date'
      },
      {
        text: '+',
        value: 'dateTime'
      }
    ]);
    var singleDataValue = SelectField.formatDataValue([
      {
        text: '',
        value: 'false'
      },
      {
        text: '',
        value: 'true'
      }
    ]);
    this.state = {
      formatOptions: formatDataValue.data,
      format: formatDataValue.value,
      pickTypeOptions: pickTypeDataValue.data,
      pickType: pickTypeDataValue.value,
      singleOptions: singleDataValue.data,
      single: singleDataValue.value,
      picWidth: 20,
      picHeight: 20,
      profileId: '67732'
    }
  }

  componentDidMount() {
    let t = this;
    if (!window.WindVane) {
      Tip.show({
        icon: 'error',
        text: 'Please open this site in alinw webview.',
        duration: 20000
      });
    }
  }

  handlerTimePickerClick() {
    let t = this;
    WindVane.call('WVNative', 'timePicker', {
      format: t.state.format[0].value, //yyyy-MM-dd  HH:mm yyyy-MM-dd HH:mm:ss
      pickType: t.state.pickType[0].value //date() time dateTime(+)
    }, function (e) {
      let timeMsg = React.findDOMNode(t.refs.timeMsg);
      timeMsg.innerHTML = JSON.stringify(e);
    });
  }

  handlerPersonPickerClick() {
    let t = this;
    WindVane.call('WVNative', 'peoplePicker', {
      'single': t.state.single[0].value,//
      'picWidth': t.state.picWidth,//0
      'picHeight': t.state.picHeight//0
    }, function (e) {
      let personMsg = React.findDOMNode(t.refs.personMsg);
      personMsg.innerHTML = JSON.stringify(e)
    })

  }

  handlerNetworkClick() {
    let t = this;
    WindVane.call('WVNative', 'getCurrentNetStatus', {}, function (e) {
      let netMsg = React.findDOMNode(t.refs.netMsg);
      netMsg.innerHTML = JSON.stringify(e);
    })
  }

  handleBackClick() {
    WindVane.call('WVNative', 'nativeBack', {}, function () {

    });
  }

  handlerProfileClick() {
    let t = this;
    WindVane.call('WVNative', 'showPersonProfile', {
      'userId': t.state.profileId
    }, function () {
    })
  }

  handlerSendMsgClick() {
    WindVane.call('WVNative', 'sendPost', {
      'bizType': 'Create', // (Create, Forward, Comment)
      'title': '', // 
      'postScope': 'public', // public, private
      'content': '', // 
      'placeholderText': '' // placeholder
    }, function () {
    })
  }

  handlerCommentClick() {
    WindVane.call('WVNative', 'sendPost', {
      'bizType': 'Comment', //(Create, Forward, Comment)
      'postId': '4998623', //id
      'srcCommentId': '2595',
      'content': 'xia dan', //
      'placeholderText': 'comment' //placeholder
    }, function () {
    });

  }

  handlerForwardsClick() {
    WindVane.call('WVNative', 'sendPost', {
      'bizType': 'Forward', //(Create, Forward, Comment)
      'groupId': '', //id
      'extraContent': {"postId": "4998627", "publisherId": "43957"}, //  id  id
      'content': 'Dodd', //
      'placeholderText': 'forward' //placeholder
    }, function () {
    });
  }

  handlerGetLocationClick() {
    var t = this;
    WindVane.call('WVNative', 'getLocation', {
      'enableHighAccuracy': false, // 
      'address': false //  data  address 
    }, function (e) {
      let getLocationDone = React.findDOMNode(t.refs.getLocationDone);
      getLocationDone.innerHTML = JSON.stringify(e);
    }, function (e) {
      let getLocationFail = React.findDOMNode(t.refs.getLocationFail);
      getLocationFail.innerHTML = JSON.stringify(e);
    })
  }

  handlerNativeCallClick() {
    WindVane.call('WVNative', 'nativeCall', {
      'ios': {
        'scheme': 'taobao://',  //  app  app download Url
        'downloadURL': '//m.taobao.com/channel/act/sale/tbdl1.html'
      },
      'android': {
        'scheme': 'taobao://',
        'downloadURL': '//m.taobao.com/channel/act/sale/tbdl1.html',
        'pkg': 'com.taobao.taobao'
      }
    }, function (e) {
    })
  }

  handlerAttachmentClick() {
    WindVane.call('WVNative', 'openAttachment', {
      'downloadUrl': 'https://a-work.alibaba-inc.com/asset/workflow/common/downloadAttachment.do?attachId=4612640877944832', // 
      'fileName': 'test.msg', // 
      'fileSize': 0, // 
      'fileType': '' // 
    }, function () {
    });

  }

  handlerVersionClick() {
    var t = this;
    WindVane.call('WVNative', 'getVersion', {}, function (e) {
      let msg = React.findDOMNode(t.refs.versionMsg);
      msg.innerHTML = JSON.stringify(e);
    }, function (e) {
      let msg = React.findDOMNode(t.refs.versionMsg);
      msg.innerHTML = JSON.stringify(e);
    });
  }

  handlerGetLocationInParkClick() {
    var t = this;
    WindVane.call('WVNative', 'getLocationInPark', {}, function (e) {
      let msg = React.findDOMNode(t.refs.getLocationInParkMsg);
      msg.innerHTML = JSON.stringify(e);
    }, function (e) {
      let msg = React.findDOMNode(t.refs.getLocationInParkMsg);
      msg.innerHTML = JSON.stringify(e);
    });
  }

  handlerNavigateToClick() {
    var t = this;
    WindVane.call('WVNative', 'navigateTo', {
      'xCoord': 120.02357733161482,//double
      'yCoord': 30.27921178524374,//double
      'floor': 5,//int
      'name': "jiushen\'s location"
    }, function (e) {
      let msg = React.findDOMNode(t.refs.navigateToMsg);
      msg.innerHTML = JSON.stringify(e);
    }, function (e) {
      let msg = React.findDOMNode(t.refs.navigateToMsg);
      msg.innerHTML = JSON.stringify(e);
    });
  }

  handlerSelectChange(k, v) {
    this.setState({
      [k]: v
    })
  }

  handlerAlertClick() {
    WindVane.call('WVUIDialog', 'alert', {"message": "", "okbutton": ""}, function (e) {
      //nop callback
    });
  }

  render() {
    let t = this;
    return (
      <div className="tPageNative">
        <div className="feature-block">
          <Group>
            <Group.Head>
              
            </Group.Head>
            <Group.List lineIndent={14} itemIndent={[14,14]}>
              <SelectField label="" data={t.state.formatOptions} value={t.state.format}
                           onChange={t.handlerSelectChange.bind(t, 'format')}/>
              <SelectField label="" data={t.state.pickTypeOptions} value={t.state.pickType}
                           onChange={t.handlerSelectChange.bind(t, 'pickType')}/>
              <div ref="timeMsg" className="tLH44 tFC6"></div>
            </Group.List>
          </Group>
          <div className="button-wrapper">
            <Button ref="timePicker" onClick={t.handlerTimePickerClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <Group>
            <Group.Head>
              
            </Group.Head>
            <Group.List lineIndent={14} itemIndent={[14,14]}><SelectField label="" data={t.state.singleOptions}
                                                                          value={t.state.single}
                                                                          onChange={t.handlerSelectChange.bind(t, 'single')}/>
              <NumberField label="" value={t.state.picWidth} onChange={t.handlerSelectChange.bind(t, 'picWidth')}/>
              <NumberField label="" value={t.state.picHeight}
                           onChange={t.handlerSelectChange.bind(t, 'picHeight')}/>
              <div ref="personMsg" className="tLH44 tFC6"></div>
            </Group.List>
          </Group>
          <div className="button-wrapper">
            <Button ref="personPicker" onClick={t.handlerPersonPickerClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44"></div>
            <Button ref="alert" onClick={t.handlerAlertClick.bind(t)}> Alert</Button>
          </div>
        </div>

        <div className="feature-block">
          <Group>
            <Group.Head>
            </Group.Head>
            <Group.List lineIndent={14} itemIndent={[14,14]}>
              <div ref="netMsg" className="tLH44 tFC6"></div>
            </Group.List>
          </Group>
          <div className="button-wrapper">
            <Button ref="network" onClick={t.handlerNetworkClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44"></div>
            <Button ref="back" onClick={t.handleBackClick.bind(t)}>WebView </Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44"></div>
            <Button ref="profile" onClick={t.handlerProfileClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44"></div>
            <Button ref="sendMsg" onClick={t.handlerSendMsgClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44"></div>
            <Button ref="comment" onClick={t.handlerCommentClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44"></div>
            <Button ref="forwards" onClick={t.handlerForwardsClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <Group>
            <Group.Head>
            </Group.Head>
            <Group.List lineIndent={14} itemIndent={[14,14]}>
              <div ref="getLocationDone" className="tLH44 tFC6"></div>
              <div ref="getLocationFail" className="tLH44 tFC6"></div>
            </Group.List>
          </Group>
          <div className="button-wrapper">
            <Button ref="getLocation" onClick={t.handlerGetLocationClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44">app</div>
            <Button ref="nativeCall" onClick={t.handlerNativeCallClick.bind(t)}> APP</Button>
          </div>
        </div>

        <div className="feature-block">
          <div className="button-wrapper">
            <div className="tLH44"></div>
            <Button ref="attachment" onClick={t.handlerAttachmentClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <Group>
            <Group.Head>
            </Group.Head>
            <Group.List lineIndent={14} itemIndent={[14,14]}>
              <div ref="versionMsg" className="tLH44 tFC6"></div>
            </Group.List>
          </Group>
          <div className="button-wrapper">
            <Button ref="version" onClick={t.handlerVersionClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <Group>
            <Group.Head>
            </Group.Head>
            <Group.List lineIndent={14} itemIndent={[14,14]}>
              <div ref="getLocationInParkMsg" className="tLH44 tFC6"></div>
            </Group.List>
          </Group>
          <div className="button-wrapper">
            <Button ref="getLocationInPark" onClick={t.handlerGetLocationInParkClick.bind(t)}></Button>
          </div>
        </div>

        <div className="feature-block">
          <Group>
            <Group.Head>
            </Group.Head>
            <Group.List lineIndent={14} itemIndent={[14,14]}>
              <div ref="navigateToMsg" className="tLH44 tFC6"></div>
            </Group.List>
          </Group>
          <div className="button-wrapper">
            <Button ref="navigateTo" onClick={t.handlerNavigateToClick.bind(t)}></Button>
          </div>
        </div>
      </div>
    )
  }
}

module.exports = Module;



